const pkg = require('../config');

const print = (msg, type) => {
    if( !pkg.debug ) return;
    if( type === 'throw' ) {
        throw msg;
    }
    /* eslint-disable */
    else if(console && console.log) {
        console.log(msg);
    }
    /* eslint-enable */
};

// 检查参数合法性
const checkParameter = (name, data, type) => {
    if( Object.prototype.toString.call(data) !== '[object '+type+']' ) {
        print('The "'+name+'" parameter must be a '+type+'.\n' + data, 'throw');
    }
};
module.exports = {
    print: print,
    check: checkParameter
};