const esCache = require('../tool/cache')('esCache');
const rRep = /[<>\"\'&]/g; // eslint-disable-line
let robj = {};

//
['&','<','>','"',"'"].forEach(function(it) { // eslint-disable-line
    const code = it.charCodeAt(0);
    robj[ code ] = '&#'+code+';';
});

/**
 * Escape
 * @param  {String} data 要处理的字符串
 * @return {String}
 */
const escape = (data) => {
    let ret;
    data += '';
    if( (ret = esCache.get(data)) ) {
        return ret;
    }
    ret = rRep.test(data) ? (data+'').replace(rRep, function(it) {
        return robj[ it.charCodeAt(0) ] || it;
    }) : data;
    esCache.set(data, ret);
    return ret;
};

module.exports = escape;