const Compiler = require('./compiler');
const filter= require('../filter/index');
const config= require('../config');
const fnCache = require('../tool/cache')();

const compile = (source, option = {}) => {
    let ret;
    source = source.trim();
    if( config.cache && (ret = fnCache.get(source)) ) {
        return ret;
    }

    // option
    for( let key in config ) {
        option[key] = config[key];
    }
    option.source = source;

    // ignore key
    let ignore = {};
    let keys = 'do,if,for,let,new,try,var,case,else,with,await,break,'+
        'catch,class,const,super,throw,while,yield,delete,export,import,return,'+
        'switch,default,extends,finally,continue,debugger,function,arguments,'+
        'instanceof,typeof,catch,void,this,in,try,';
    keys += option.ignore;
    keys.replace(/[A-Za-z_$](?:\w+)?/g, function(word) {
        ignore[word] = true;
    });
    option.ignore = ignore;

    ret = new Compiler(option).bind(filter);
    config.cache && fnCache.set(source, ret);
    return ret;
};

module.exports = compile;