/**
 * 计算长度
 * @param  {Object | Array} data 数组或者对象
 * @return {Number}
 */
const size = (data) => {
    let size = data && data.length;
    if( !size && typeof size === 'object' ) {
        size = 0;
        for( let key in data ) { // eslint-disable-line
            size ++;
        }
    }
    return size || 0;
};

module.exports = size;