const wrapCache = require('../tool/cache')('wrapCache');
const rRep = /[\r\n\'\"]/g; // eslint-disable-line

/**
 * 处理换行符与引号
 * @param  {String} data 要处理的字符串
 * @return {String}
 */
const rwrap = (data) => {
    let ret;
    data += '';
    if( (ret = wrapCache.get(data)) ) {
        return ret;
    }
    ret = data.replace(rRep, function(it, index, detail) {
        const code = it.charCodeAt(0);
        let ret;
        if( code === 0x27 || code === 0x22 ) { // ' or "
            ret = detail.charCodeAt(index-1) !== 0x5C ? '\\' + it : it;
        }
        else {
            ret = '\\' + (code === 0xa ? 'n': 'r');
        }
        return ret;
    });
    wrapCache.set(data, ret);
    return ret;
};

module.exports = rwrap;