/**
 * 替换成大写
 * @param  {String} data 要处理的字符串
 * @param  {Boolean} flag 可选参数(不传：全部替换；true: 替换每个单词首字母)
 * @return {String}
 */
const upper = (data, flag) => {
    let ret;
    data += '';
    /* eslint-disable */
    switch(flag) {
        case true:
            ret = data.replace(/\b[a-z]\w*\b/g, function(word) {
                return word.substr(0,1).toUpperCase() + word.substr(1);
            });
            break;
        default:
            ret = data.toUpperCase();
    }
    return ret || data;
    /* eslint-enable */
};

module.exports = upper;