/**
 * 截断字符串
 * @param  {String} str 要处理的字符串
 * @param  {Number} length 默认长度,超出将执行截断操作
 * @param  {String} end 被截断的字符串替换字符
 * @return {String}
 */
const truncate = (data, length=255, end='...') => {
    data += '';
    return data.length > length ? data.substr(0, length) + end : data;
};

module.exports = truncate;